import { useState } from "react";
import AddIcon from '@mui/icons-material/Add';
import EditIcon from '@mui/icons-material/Edit';
import DeleteIcon from '@mui/icons-material/Delete';
import styles from '@/views/partials/card.module.css';
import clsx from 'clsx';


function Card() {
    const [isExpanded, setExpanded] = useState(false);
    function expand() {
        setExpanded(true);
      }
      function collapse() {
        setExpanded(false);
      }
      const [card, setCard] = useState({
        task: "",
        description: "",
        dueDate: ""
      });
    function handleChange(event: React.ChangeEvent<HTMLInputElement>) {
         const { name, value } = event.target;
     
         setCard((prevCard) => {
           const newCard = {
             ...prevCard,
             [name]: value
           };
           console.log(newCard);
           return newCard;
         });
       }

    return (
        <div className={styles.content}>
            <div className={styles.cardParent} style={{height: isExpanded? "300px": '' }}>
                <div className={styles.card} onClick={() => collapse()} >
                   <input className={styles.input}
                      type="text"
                      name="task"
                      value={card.task}
                      onChange={handleChange}
                      onClick={expand}
                      placeholder="Add a task"
                      style={{background: "transparent"}}
                   />

                   <input className={styles.input}
                    type="text"
                     name="description"
                       value={card.description}
                       onChange={handleChange}
                     placeholder="Add a description"
                     style={{display: isExpanded? '': "none", background: "transparent"}}
                     />
                     <div className={styles.bottomSection} >

                    <input className={clsx(styles.input, styles.date)} style={{display: isExpanded? '': "none" }} 
                        type="date"  
                        onChange={handleChange}
                        value={card.dueDate}
                        name="dueDate" 
                    />
                    <select className={clsx(styles.select,styles.priority)} style={{display: isExpanded? '': "none" }}>
                        <option value="" disabled hidden>Priority</option>
                        <option value="low">Low</option>
                        <option value="medium">Medium</option>
                        <option value="high">High</option>
                    </select>
                    </div>



                    <div className={`${styles.icons}`}>
                          <button className={clsx(styles.add, styles['my-button'])} style={{display: isExpanded? '': "none"}}>
                              <AddIcon style={{ color: 'white' }} />
                          </button>
                          <button className={clsx(styles.edit, styles['my-button'])} style={{display: isExpanded? '': "none"}}>
                              <EditIcon style={{ color: 'white' }} />
                          </button>
                          <button className={clsx(styles.delete, styles['my-button'])} style={{display: isExpanded? '': "none"}} >
                              <DeleteIcon style={{ color: 'white' }} />
                          </button>
                    </div>    
                </div>
            </div>
        </div>
    );
};

export default Card;
